.Navbar{
    position: absolute;
    z-index: 0;
    margin: 0;
    padding: 0;
    width: 100%;
    height: 75px;
    display: flex;
    justify-content:space-evenly;
    align-items: center;
}
.nav-container{
    display: flex;
    margin-right: 100px;
}
.nav-li{
    padding: 10px;
    list-style: none;
}
.nav-a{
    color: gray;
    text-decoration: none;
    font-weight: bold;
}
.nav-a:hover{
    color: aqua;
    border-bottom: 2px solid aqua;
}

.login-btn{
    width: 120px;
    height: 40px;
    border-radius: 10px;
    border: transparent;
    color: aqua;
    font-family: Arial, Helvetica, sans-serif;
    font-weight: bold;
    font-size: 18px;
    text-transform: capitalize;

}
.login-btn:hover ,.member-btn:hover{
    background-color: rgb(16, 175, 238);
    transition: all ease 500ms;
    color: white;
    cursor: pointer;
}
.member-btn{
    width: 200px; 
    height: 40px;
    margin-left: 10px; 
    border-radius: 10px;
    border: transparent;
    background-color: aqua; 
    color: white;
    font-family: Arial, Helvetica, sans-serif;
    font-weight: bold;
    font-size: 16px;
    text-transform: capitalize
}
.mobile-Navbar,.mobile-nav{
    display: none;
    /* display: flex; */
    justify-content: center;
    align-items: center;
}
#mobile-nav-li{
    padding: 10px;
}


/* Responsive Navbar */
@media screen and (max-width : 1200px) {
    .nav-container,.login-btn,.member-btn{
        display: none;
    }
    .Navbar{
        align-items: center;
        border: 2px solid red;
        margin: 0;
        padding: 0;
        justify-content: space-around;
    }
    .mobile-nav{
        display: inherit;
        align-items: center;
        justify-content: center;
    }
    #mobile-nav-li{
        padding: 10px;  
    }
    .nav-container{
        border: 2px solid green;
        position: fixed;
        width: 100%;
        height: 100vh;
        top: 80px;
        padding: 50px 0;
        left: -100%;
        transition: all .5s;
        display: inherit;
        flex-direction: column;
        align-items: center;
        background: rgb(32, 194, 145);
    }
    #mobile-nav-li:hover .nav-container{
        left: 0;
    }
    
}
    
